import { Pipe, PipeTransform } from '@angular/core';

export interface opt  { 
  itemsPerPage : any,
  PageKey : any,
}

@Pipe({
  name: 'pipePagination'
})
export class PipePaginationPipe implements PipeTransform {

  transform(value:Array<any>, args : opt) {
    console.log(args) 
    let startIndex : any = 0 ;
    let endIndex : any = 0 ;
    if(value.length > args.itemsPerPage){
      if(args.PageKey != 0 ){
        startIndex =  (args.PageKey-1) * args.itemsPerPage ;
      }else{
    startIndex =  args.PageKey * args.itemsPerPage ;
  }
    endIndex =  (startIndex + (args.itemsPerPage - 1)) ; 
    return value.filter((item,index)=> {
      return((index >=  startIndex)  &&  (index <=  endIndex))
    });
  }else {
    return value;
  }
    
  }

}
