import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'ng9-pageControls',
  templateUrl: './page-controls.component.html',
  styleUrls: ['./page-controls.component.scss']
})
export class PageControlsComponent implements OnInit {
  @Input() pageKey : any ; 
  @Input() arrayLength  : any ;
  @Input() itemsPerPage : any ;
  @Output() pageChange:EventEmitter<any> = new EventEmitter() ;

  totalPages : any=0;

  constructor() { }

  ngOnInit(): void {
   this.totalPages= Math.round(this.arrayLength/this.itemsPerPage);

  }
  onNext(){
    if(this.pageKey < this.totalPages ){
     if( this.pageKey++ == this.totalPages){
       //disableNextBUtton
     }
     
      this.pageChange.emit(this.pageKey++)
    }
  }
  
  prevPage(){
    if((this.pageKey-1) > 0){
      this.pageChange.emit(this.pageKey - 1);
    }
  }
  enablePrev(){
    if(this.pageKey == 1){
      return true;

    }
    return false;
  }
  enableNext(){
    if(this.pageKey == this.totalPages){
      return true;

    }
    return false;
  }

}
